import {CommonAPI} from "./CommonAPI";

const web3 = {
    fromWei: (n: any) => n,
};

export class CommonAPIMock implements CommonAPI {
    
    async getWeb3(): Promise<any> {
        return web3;
    }

    async getTTBalance(): Promise<number> {
        return 9999;
    }

    async getTokenBalance(): Promise<number> {
        return 5555;
    }

    public async getAccount(): Promise<string[]> {
        return [
            'player3',
        ];
    }

    submitGame(params: any): Promise<void> {
        return Promise.resolve(params);
    }
}

export default new CommonAPIMock();
